.hello__title {
    font-family: 'Buyan';
    margin: 0 auto;
    color: rgba(77, 189, 252, 1);
    font-size: 96px;
    font-weight: 500;
    line-height: 48px;
    letter-spacing: calc(1em / 5);
    text-transform: uppercase;
}

.hello__title-span {
    margin: 25px auto 0;
    color: rgba(221, 29, 29, 1);
}

.hello__subtitle {
    margin: 46px auto 0;
    font-weight: 800;
    font-size: 24px;
    line-height: 36px;
    letter-spacing: .015em;
}

@media screen and (max-width: 768px) {
    .hello__title {
        max-width: 768px;
        line-height: 75px;
        letter-spacing: .18em;
    }
    .hello__title-span {
        margin: 5px 0 0 10px;
        font-size: 75px;
        letter-spacing: .18em;
    }
}

@media screen and (max-width: 320px) {
    .hello__title {
        letter-spacing: .15em;
    }
    .hello__title-span {
        margin: 0;
        letter-spacing: .09em;
    } 
}